.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.nav {
    height:100%;
    width:100%;
    /* position: relative; */
    
}
.buttons {
    position: absolute;
    width:20rem;

    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.nav-button {
    height: 10rem;
    padding-top: 10%;
    margin-top: 10%;
    margin-bottom: 10%;
}